<script>
import Vue from 'vue'
import VueGapi from 'vue-gapi'
let doRevr = ( sIn = "tacocat" ) => ( sIn.split("").reverse().join("") );
Vue.use(VueGapi, {
  apiKey: doRevr("wySB8UJSJTTR2d_k_uN_KWLdDtsYZutUDySazIA"),
  clientId: doRevr("moc.tnetnocresuelgoog.sppa.2n5jn3qn53b76h5sqc92ae7v02qjpep3-277572845182"),
  discoveryDocs: ['https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest'],
  scope: 'https://www.googleapis.com/auth/gmail.readonly',
})

  export default {
    data: () => ({
      cards: ['Email Summary', 'Email Visualisation'],
      drawer: true,
      links: [
        ['mdi-inbox-arrow-down', 'Inbox'],
        ['mdi-send', 'Send'],
        ['mdi-delete', 'Trash'],
        ['mdi-alert-octagon', 'Spam'],
      ],
      aEmailSummaryHeaders: [  //  See Payload > Headers
        {value:"From", text:"From", sortable:true},
        {value:"DeliveredTo", text:"Recipient"},
        {value:"Date", text:"Date"},
        {value:"To", text:"To"},
        {value:"Subject", text:"Subject"}
        ],
      aEmailSummary: [  //  See Payload > Headers
       {"MessageID":"qq", "DeliveredTo":"qq", "Date":"qq", "From":"qq", "To":"qq", "Subject":"qq"}]
    }),
    methods: {
      login() {
        this.$gapi.login().then(({ currentUser, hasGrantedScopes }) => {
          console.log("currentUser, hasGrantedScopes" )
          console.log({ currentUser, hasGrantedScopes })
        })
      },
      toggleDrawer(){
        this.drawer = !this.drawer
      },
      toggleTheme(){
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      },
      fetchGmailMessages(){
        let self = this
        let goglClient = this.$gapi.clientProvider.client;

        var user = goglClient.authInstance.currentUser.get();
        var oauthToken = user.getAuthResponse().access_token;
        console.log(oauthToken);
        var xhr = new XMLHttpRequest();
        var userId='me';
        xhr.open('GET',
            'https://gmail.googleapis.com/gmail/v1/users/'+userId+'/messages');
        xhr.setRequestHeader('Authorization',
            'Bearer ' + oauthToken);
        xhr.send();
        xhr.onload=function () {
            var data=JSON.parse(this.response);
            for (let i in data.messages)
            {
     //  console.log(data.messages[i].id,userId,oauthToken);
              self.fetchMailDetails(data.messages[i].id,userId,oauthToken);
            }
        }
      },
      fetchMailDetails(id,userId,oauthToken){
        let self = this
        let xhr1 = new XMLHttpRequest();
        xhr1.open('GET','https://gmail.googleapis.com/gmail/v1/users/'+userId+'/messages/'+id,true);
        xhr1.setRequestHeader('Authorization', 'Bearer ' + oauthToken);
        xhr1.send();
        xhr1.onload=function(){
          let emailDetails=JSON.parse(this.response)
          let emailDetHeaders = emailDetails.payload.headers
console.warn( emailDetails );
window.qqq = emailDetHeaders;
          self.aEmailSummary.push({
            "MessageID":self.extractVal(emailDetHeaders, "Message-ID"),
            "DeliveredTo":self.extractVal(emailDetHeaders, "Delivered-To"),
            "Date":self.extractVal(emailDetHeaders, "Date"),
            "From":self.extractVal(emailDetHeaders, "From"),
            "To":self.extractVal(emailDetHeaders, "To"),
            "Subject":self.extractVal(emailDetHeaders, "Subject")
          })
          //  var from=getHeader(emailDetails.payload.headers,"From");
          //  var subject=getHeader(emailDetails.payload.headers,"Subject");
          //  var date=getHeader(emailDetails.payload.headers,"Date");
          //  appendMailRow(from,subject,date);
        }
      },
      extractVal(aHead, sName){
        let sVal = aHead.filter((v)=> (v.name===sName))
        return (sVal[0] && sVal[0].value) ? sVal[0].value : ""
      }

    }
  }
</script>
